var PieChart = require('react-d3-components').PieChart;

var data = {
    label: 'somethingA',
    values: [{ x: 'SomethingA', y: 10 }, { x: 'SomethingB', y: 4 }, { x: 'SomethingC', y: 3 }]
};

module.exports = React.createClass({
    render: function() {
        return (
            
    <PieChart
        data={data}
                width={600}
                height={400}
                margin={{top: 10, bottom: 10, left: 100, right: 100}}
                />
            );
    }
});


